import { AppState } from "./appState";
import { Action, StoreAction } from './store';
import { newGuid } from "../Helpers/guidHelper";
import { useDispatch, useSelector } from "react-redux";
import { Dispatch } from "react";

// *** Model ***

export interface TaskNoId {
  title: string;
}

export interface Task extends TaskNoId {
  id: string;
}

export interface TaskModel {
  [id: string]: Task;
}

// *** Actions ***

interface UpdateTaskAction extends Action {
  type: "UpdateTask";
  id: string;
  task: Partial<TaskNoId>;
}

interface CreateTaskAction extends Action {
  type: "CreateTask";
  task: TaskNoId;
}

export type TaskActions = UpdateTaskAction | CreateTaskAction;

// *** Reducer ***

function updateTask(tasks: TaskModel, id: string, updates: Partial<TaskNoId>) {
  var task = tasks[id];

  if (!task) throw new Error(`No task found with id {id}`);

  tasks[id] = {
    ...task,
    ...updates,
  };

  return { ...tasks };
}

function createTask(tasks: TaskModel, task: TaskNoId) {
  const id = newGuid();

  tasks[id] = {
    ...task,
    id
  };

  return { ...tasks };
}

export function taskReducer(tasks: TaskModel = {}, action: StoreAction) {
  switch (action.type) {
    case "UpdateTask": return updateTask(tasks, action.id, action.task);
    case "CreateTask": return createTask(tasks, action.task);
    default: return tasks;
  }
}

// *** Service ***

export const useTasks = () => useSelector<AppState, TaskModel>(appState => appState.tasks);

export interface TaskService {
  createTask: (task: TaskNoId) => void;
  updateTask: (id: string, task: Partial<TaskNoId>) => void;
}

export function useTaskService(): TaskService {
  const dispatch = useDispatch<Dispatch<TaskActions>>();

  return {
    createTask: task => dispatch({ type: "CreateTask", task }),
    updateTask: (id, task) => dispatch({ type: "UpdateTask", id, task }),
  };
}
