import { Reducer } from "react"
import { AppState } from "./appState"
import { StoreAction, Action } from "./store"
import { useDispatch, useSelector } from "react-redux";
import { ThunkDispatch } from "redux-thunk";
import { dataStore } from "../DataStore/DataStoreService";
import { StoreModel } from "../DataStore/dataStoreModel";
import { Task, TaskModel } from "./tasks";

// *** Model ***

export interface DataStorageModel {
  status: "fetching" | "saving" | "inactive";
  fetchError?: any;
  saveError?: any;
}

// *** Actions ***

interface ModelFetchingAction extends Action {
  type: "ModelFetching";
}

interface ModelFetchedAction extends Action {
  type: "ModelFetched";
  appState: Omit<AppState, "dataStorage">;
}

interface ModelFetchErrorAction extends Action {
  type: "ModelFetchError";
  error: any;
}

interface ModelSavingAction extends Action {
  type: "ModelSaving";
}

interface ModelSavedAction extends Action {
  type: "ModelSaved";
}

interface ModelSaveErrorAction extends Action {
  type: "ModelSaveError";
  error: any;
}

export type DataStorageActions =
  ModelFetchingAction |
  ModelFetchedAction |
  ModelFetchErrorAction |
  ModelSavingAction |
  ModelSavedAction |
  ModelSaveErrorAction;

// *** Reducers ***

export const dataStorageReducer: Reducer<AppState, StoreAction> = (appState, action): AppState => {
  if (appState === undefined) throw new Error("dataStorageReducer: Unexpected undefined appState.");

  switch (action.type) {
    case "ModelFetching": return { ...appState, dataStorage: { ...appState.dataStorage, status: "fetching", fetchError: undefined } };
    case "ModelFetched": return { ...action.appState, dataStorage: { ...appState.dataStorage, status: "inactive", fetchError: undefined } };
    case "ModelFetchError": return { ...appState, dataStorage: { ...appState.dataStorage, status: "inactive", fetchError: action.error } };
    case "ModelSaving": return { ...appState, dataStorage: { ...appState.dataStorage, status: "saving", saveError: undefined } };
    case "ModelSaved": return { ...appState, dataStorage: { ...appState.dataStorage, status: "inactive", saveError: undefined } };
    case "ModelSaveError": return { ...appState, dataStorage: { ...appState.dataStorage, status: "inactive", saveError: action.error } };
    default: return appState;
  }
}

// *** Service ***

export const useDataStorage = () => useSelector<AppState, DataStorageModel>(appState => appState.dataStorage);

function getStoreModel(appState: AppState): StoreModel {
  const tasks: Task[] = [];
  for (const taskId in appState.tasks) {
    tasks.push(appState.tasks[taskId]);
  }

  return { tasks };
}

function getAppState(storeModel: StoreModel): Omit<AppState, "dataStorage"> {
  const tasks: TaskModel = {}

  storeModel.tasks.forEach(task => tasks[task.id] = task);

  return { tasks };
}

export const fetchData = (thunkDispatch: ThunkDispatch<AppState, undefined, StoreAction>) => {
  thunkDispatch(async (dispatch, getState) => {
    const state = getState();
    if (state.dataStorage.status !== "inactive") {
      throw Error("An action is already ongoing.");
    }

    dispatch({ type: "ModelFetching" });

    try {
      const appState = await dataStore.getData();
      dispatch({ type: "ModelFetched", appState: getAppState(appState) });
    }
    catch (error) {
      dispatch({ type: "ModelFetchError", error });
    }
  });
}

export function useFetchData() {
  const thunkDispatch = useDispatch<ThunkDispatch<AppState, undefined, StoreAction>>();
  return () => fetchData(thunkDispatch);
}

export function useSaveData() {
  const thunkDispatch = useDispatch<ThunkDispatch<AppState, undefined, StoreAction>>();

  return () => thunkDispatch(async (dispatch, getState) => {
    const appState = getState();
    if (appState.dataStorage.status !== "inactive") {
      throw Error("An action is already ongoing.");
    }

    dispatch({ type: "ModelSaving" });

    try {
      await dataStore.saveData(getStoreModel(appState));
      dispatch({ type: "ModelSaved" });
    }
    catch (error) {
      dispatch({ type: "ModelSaveError", error });
    }
  });
}
