import React, { useState, useMemo } from "react";
import "./Tasks.css";
import { useTaskService, Task, TaskNoId } from "../../Store/tasks";
import { Dialog } from "../Utility/Dialog/Dialog";
import { Field } from "../Utility/Field/Field";

export type TaskFormProps = {
  task: Task | undefined;
  onClose: () => void;
};

export function TaskForm(props: TaskFormProps) {
  const id = useMemo(() => props.task ? props.task.id : undefined, [props.task]);
  const [editedTask, setEditedTask] = useState<TaskNoId>(props.task || { "title": "" });
  const { createTask, updateTask } = useTaskService();

  const onSave = () => {
    if (id) {
      updateTask(id, editedTask);
    } else {
      createTask(editedTask);
    }
    props.onClose();
  }

  return <Dialog title={id ? "Modifier une tâche" : "Nouvelle tâche"} buttons={[
    { text: "Valider", onclick: onSave },
    { text: "Annuler", onclick: props.onClose }
  ]}>
    <>
      <Field label="titre" >
        <input type="text" style={{ width: "100%", fontSize: "12pt" }} value={editedTask.title} onChange={event => setEditedTask({ title: event.target.value })} />
      </Field>
    </>
  </Dialog>;
}
